html {
  font-family: "Fredoka One", sans-serif;
}
body {
  background-color: #0089da;
  padding: 0;
  padding-bottom: 100px;
  margin: 0;
}
body,
html {
  overflow-x: hidden;
  overflow-y: hidden;
}
textarea {
  font-size: 24px;
  box-sizing: border-box;
}

canvas {
  pointer-events: none;
  position: absolute;
}

#polygon_canvas {
  margin: 0 auto;
  text-align: center;
}

.topBar {
  background-color: #2cb2f1;
  box-shadow: 0px 5px rgba(18, 86, 240, 0.7);
  height: 30px;
  line-height: 30px;
  text-align: center;
}

.icon {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  color: #eee;
  text-shadow: 2px 2px 2px #5c23f0;
  text-align: center;
  position: absolute;
}
.window {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  pointer-events: auto;
  position: absolute;
  touch-action: none;
  box-sizing: border-box;
}
.box {
  background-color: #2cb2f1;
  box-sizing: border-box;
}
.box--large {
  width: 270px;
  height: 270px;
}
.box--medium {
  width: 180px;
  height: 180px;
}
.box--small {
  width: 90px;
  height: 90px;
}
.notepad {
  resize: none;
}
.exit-icon {
  margin-top: 0px;
  margin-right: 5px;
}
.exit-icon:hover {
  color: #a920f1;
}

.focussed {
  border: 3px solid #55d3f1;
  box-shadow: 3px 3px #5c23f0, 10px 10px rgba(18, 86, 240, 0.7),
    #5c23f0 3px 3px inset, #5c23f0 3px 3px inset, #a920f1 0px 0px 0px 3px inset;
}

.unfocussed {
  border: 3px solid #f066c9;
  box-shadow: 3px 3px #5c23f0, 10px 10px rgba(18, 86, 240, 0.7),
    #5c23f0 3px 3px inset, #5c23f0 3px 3px inset, #a920f1 0px 0px 0px 3px inset;
}

.titlebar {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  /* box-shadow: 3px 3px #5c23f0, 10px 10px rgba(18, 86, 240, 0.7),  #5c23f0 3px 3px inset, #5c23f0 3px 3px inset, #a920f1 0px 0px 0px 3px inset; */
  height: 25px;
  box-shadow: 10px 10px rgba(18, 86, 240, 0.7);
  /* border: 3px solid #55d3f1; */
  padding-left: 5px;
  border-bottom: 0;
  line-height: 25px;
  white-space: nowrap;
  overflow: hidden;
  color: #5c23f0;
  background-color: #ffb7de;
}
